/* src/Checkbox/Checkbox.module.css */
.Checkbox_root {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: var(--rfe-spacing);
}
.Checkbox_inputRoot {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.Checkbox_label {
  margin-top: 1px;
  font-size: var(--rfe-font-size);
  line-height: 20px;
  color: var(--rfe-color);
  pointer-events: all;
  cursor: pointer;
}
.Checkbox_box {
  width: 28px;
  height: 28px;
  border: solid 1px var(--rfe-border);
  border-radius: var(--rfe-border-radius);
  margin-right: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--rfe-white);
  flex-shrink: 0;
  background-color: var(--rfe-background);
  transition: all 250ms;
  &:hover {
    box-shadow: var(--rfe-focus);
  }
}
.Checkbox_inputError {
  border-color: var(--rfe-color-error) !important;
}
.Checkbox_boxDisabled {
  background-color: var(--rfe-background-disabled);
  &:hover {
    box-shadow: none;
    cursor: not-allowed;
  }
}
.Checkbox_icon {
  display: none;
  line-height: 1;
  color: var(--rfe-color);
}
.Checkbox_input {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
  opacity: 0;
  width: 0;
  height: 0;
}
.Checkbox_input:checked ~ .Checkbox_box {
  background-color: var(--rfe-background-selected);
  border-color: var(--rfe-border);
}
.Checkbox_input:checked ~ .Checkbox_box .Checkbox_icon {
  display: block;
}
.Checkbox_input:focus-visible ~ .Checkbox_box {
  box-shadow: var(--rfe-focus);
}
.Checkbox_errorLabel {
  color: var(--rfe-color-error);
  font-size: var(--rfe-error-font-size);
  margin-top: var(--rfe-label-spacing);
  pointer-events: none;
}

