/* src/Input/Input.module.css */
.Input_root {
  margin-bottom: var(--rfe-spacing);
}
.Input_inputRoot {
  position: relative;
}
.Input_label {
  font-size: var(--rfe-font-size);
  line-height: 1;
  color: var(--rfe-color);
  margin-bottom: 0;
  position: absolute;
  top: -2px;
  left: 16px;
  cursor: text;
  transform: translateY(calc((var(--rfe-input-height) - 14px) / 2));
  transition: all 250ms;
}
.Input_label.Input_disableShrink {
  display: block;
  position: static;
  margin-bottom: var(--rfe-label-spacing);
  color: var(--rfe-color);
  transform: none;
}
.Input_prepend ~ .Input_placeholder {
  padding: 24px 16px 0 60px !important;
}
.Input_placeholder {
  padding: 19px 16px 0 !important;
}
.Input_labelPlaceholder {
  font-size: 12px;
  transform: translateY(9px);
  top: 0;
}
.Input_input {
  width: 100%;
  font-size: 14px;
  line-height: 24px;
  color: var(--rfe-color);
  padding: 19px 16px 0;
  background: var(--rfe-background);
  border: 1px solid var(--rfe-border);
  border-radius: var(--rfe-border-radius);
  height: var(--rfe-input-height);
  transition: all 250ms;
  font-family: var(--rfe-font-family);
}
.Input_input:focus {
  box-shadow: var(--rfe-focus);
}
.Input_input::-moz-placeholder {
  color: var(--rfe-color-placeholder);
  font-size: 14px;
}
.Input_input::placeholder {
  color: var(--rfe-color-placeholder);
  font-size: 14px;
}
.Input_input:-webkit-autofill:not(.Input_disableShrink) ~ .Input_label,
.Input_input:focus:not(.Input_disableShrink) ~ .Input_label,
.Input_input:not([value=""], .Input_disableShrink) ~ .Input_label {
  font-size: 12px;
  transform: translateY(9px);
}
.Input_input.Input_disableShrink {
  padding: 16px;
}
.Input_append,
.Input_prepend {
  position: absolute;
  top: 50%;
  color: var(--rfe-color);
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}
.Input_prepend {
  left: 16px;
  font-size: 14px;
  line-height: 16px;
  padding-right: 13px;
  border-right: 1px solid var(--rfe-border);
  height: var(--rfe-input-height);
}
.Input_prepend ~ .Input_label {
  left: 62px;
}
.Input_prepend ~ .Input_input {
  padding: 20px 16px 0 60px !important;
}
.Input_prepend ~ .Input_input.Input_disableShrink {
  padding: 16px 16px 16px 60px !important;
}
.Input_append {
  right: 0;
  height: 56px;
  min-width: 36px;
}
.Input_append ~ .Input_input {
  padding: 19px 56px 0 16px;
}
.Input_append ~ .Input_input.Input_disableShrink {
  padding: 0 50px 0 16px;
}
.Input_appendDisabledShrink {
  top: 24px;
  transform: none;
}
.Input_prependDisabledShrink {
  top: 24px;
  transform: none;
}
.Input_errorLabel {
  color: var(--rfe-color-error);
  font-size: var(--rfe-error-font-size);
  margin-top: var(--rfe-label-spacing);
  pointer-events: none;
}
.Input_inputError {
  border-color: var(--rfe-color-error) !important;
}
.Input_disabled {
  background: var(--rfe-background-disabled);
  color: var(--rfe-color);
  cursor: not-allowed;
}

/* src/PasswordInput/PasswordInput.module.css */
.PasswordInput_toggle {
  background: none;
  border: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: all;
  cursor: pointer;
}
.PasswordInput_iconEye {
  color: var(--rfe-color);
  padding: 16px 0;
}
.PasswordInput_eyeOpen button::after {
  content: "Close";
}
.PasswordInput_eyeClose button::after {
  content: "Open";
}
.PasswordInput_eye {
  --duration-blink: 0.2s;
  --duration-lashes: 0.2s;
  --delay-lashes: var(--duration-blink);
  --duration-pupil: 0.1s;
  --delay-pupil: calc(var(--duration-blink) * 2 / 3);
}
.PasswordInput_eyeBottom,
.PasswordInput_eyeTop {
  stroke-linecap: round;
}
.PasswordInput_eyeTop,
.PasswordInput_eyeLashes {
  transition: var(--duration-blink) ease-in;
}
.PasswordInput_eyePupil {
  opacity: 0;
  transition: opacity var(--duration-pupil) var(--delay-pupil) ease;
}
.PasswordInput_eyeOpen .PasswordInput_eyeTop,
.PasswordInput_eyeOpen .PasswordInput_eyeLashes {
  transform: rotateX(0.5turn);
  animation: PasswordInput_scale-up var(--duration-lashes) var(--delay-lashes) ease-in-out;
}
.PasswordInput_eyeOpen .PasswordInput_eyePupil {
  opacity: 1;
}
.PasswordInput_eyeClose .PasswordInput_eyeLashes {
  animation: PasswordInput_scale-down var(--duration-lashes) var(--duration-blink) ease-in-out;
}
.PasswordInput_eyeClose .PasswordInput_eyePupil {
  opacity: 0;
}
@keyframes PasswordInput_scale-up {
  50% {
    transform: rotateX(0.5turn) scaleY(1.15);
  }
  100% {
    transform: rotateX(0.5turn) scaleY(1);
  }
}
@keyframes PasswordInput_scale-down {
  50% {
    transform: scaleY(1.15);
  }
  100% {
    transform: scaleY(1);
  }
}

